.donation-section {
    margin-top: 76px;
    background: linear-gradient(
        rgba(255, 255, 255, 0.9),
        rgba(255, 255, 255, 0.9)
      ),
      url('../images/FundrisingC.jpg');
    background-size: cover;
    background-position: center;
    padding: 4rem 0;
    min-height: calc(100vh - 76px);
  
    /* Prevent mouse wheel zoom on the background for the input spinner */
    background-attachment: fixed;
  }
  
  /* If the background is still zooming on certain devices,
     you can remove `background-attachment: fixed;` or
     force no scrolling:
  .no-zoom {
    overscroll-behavior: none;
  }
  */
  
  .donation-card {
    background: rgba(255, 255, 255, 0.95);
    border-radius: 15px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
    padding: 2rem;
  }
  
  .donation-options {
    text-align: center;
  }
  
  .donation-options h3 {
    font-weight: 600;
  }
  
  .donation-amount button.selected {
    background-color: var(--primary-color);
    color: white;
  }
  
  .custom-amount {
    max-width: 300px;
    margin: 0 auto;
  }
  
  .donation-info {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 2rem;
  }
  
  .alert-info {
    background-color: rgba(var(--primary-color), 0.1);
    border: none;
    border-radius: 10px;
  }
  
  .alert-info h4 {
    color: var(--primary-color);
  }
  
  .alert-info ul {
    padding-left: 1.5rem;
  }
  
  .alert-info li {
    margin-bottom: 0.5rem;
  }
  
  /* PayPal Button Container */
  #paypal-button-container {
    max-width: 500px;
    margin: 0 auto;
  }
  
  /* Disable "spin zoom" on number input if needed */
  input[type="number"] {
    -moz-appearance: textfield; /* removes up/down arrows in older Firefox */
  }
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none; /* removes spin buttons in Chrome/Safari/Edge */
    margin: 0;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .donation-section {
      padding: 2rem 1rem;
      background-attachment: scroll; /* avoid background shifting on mobile */
    }
  
    .donation-card {
      padding: 1.5rem;
    }
  
    .btn {
      width: 100%;
      margin-bottom: 0.5rem;
    }
  }
  